'use client';

import { useEffect, useState } from 'react';
import { collection, getDocs, query, limit } from 'firebase/firestore';
import { db } from '@/lib/firebase';
import Link from 'next/link';

export default function DashboardPage() {
  const [stats, setStats] = useState({
    totalCurrencies: 0,
    activeMedia: 0,
    totalUsers: 0,
  });

  useEffect(() => {
    const fetchStats = async () => {
      try {
        const currenciesSnap = await getDocs(collection(db, 'currencies'));
        const mediaSnap = await getDocs(
          query(collection(db, 'media'), limit(1))
        );
        const usersSnap = await getDocs(collection(db, 'users'));

        setStats({
          totalCurrencies: currenciesSnap.size,
          activeMedia: mediaSnap.size,
          totalUsers: usersSnap.size,
        });
      } catch (error) {
        console.error('Error fetching stats:', error);
      }
    };

    fetchStats();
  }, []);

  return (
    <div>
      <h2 className="text-3xl font-bold text-gray-800 mb-6">Dashboard Overview</h2>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
        <div className="bg-white rounded-lg shadow p-6">
          <h3 className="text-gray-600 text-sm font-medium mb-2">
            Total Currencies
          </h3>
          <p className="text-3xl font-bold text-primary">{stats.totalCurrencies}</p>
        </div>

        <div className="bg-white rounded-lg shadow p-6">
          <h3 className="text-gray-600 text-sm font-medium mb-2">
            Active Media
          </h3>
          <p className="text-3xl font-bold text-primary">{stats.activeMedia}</p>
        </div>

        <div className="bg-white rounded-lg shadow p-6">
          <h3 className="text-gray-600 text-sm font-medium mb-2">
            Total Users
          </h3>
          <p className="text-3xl font-bold text-primary">{stats.totalUsers}</p>
        </div>
      </div>

      {/* Quick Actions */}
      <div className="bg-white rounded-lg shadow p-6">
        <h3 className="text-xl font-bold text-gray-800 mb-4">Quick Actions</h3>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <Link
            href="/admin/dashboard/rates"
            className="p-4 border-2 border-gray-200 rounded-lg hover:border-primary hover:bg-blue-50 transition"
          >
            <h4 className="font-semibold text-gray-800 mb-2">Update Rates</h4>
            <p className="text-sm text-gray-600">
              Modify currency exchange rates
            </p>
          </Link>

          <Link
            href="/admin/dashboard/media"
            className="p-4 border-2 border-gray-200 rounded-lg hover:border-primary hover:bg-blue-50 transition"
          >
            <h4 className="font-semibold text-gray-800 mb-2">Manage Media</h4>
            <p className="text-sm text-gray-600">
              Upload images or add YouTube videos
            </p>
          </Link>

          <Link
            href="/admin/dashboard/users"
            className="p-4 border-2 border-gray-200 rounded-lg hover:border-primary hover:bg-blue-50 transition"
          >
            <h4 className="font-semibold text-gray-800 mb-2">Manage Users</h4>
            <p className="text-sm text-gray-600">
              Add or remove admin users
            </p>
          </Link>
        </div>
      </div>

      {/* View Rate Board */}
      <div className="mt-6">
        <Link
          href="/"
          target="_blank"
          className="inline-block bg-primary text-white px-6 py-3 rounded-lg font-semibold hover:bg-primary-dark transition"
        >
          View Public Rate Board
        </Link>
      </div>
    </div>
  );
}
